body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
}

.App{
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  border: 1px solid #000;
}

.usersContainer{
  display: flex;
  flex-direction: column;
  padding: 80px 0;
}

.addUserBtn{
  max-width: 200px;
  width: 100%;
  border: 0;
  background: #82B541; /* Old browsers */
  color: #ffffff;
  font-size: 14px;
  padding: 14px 20px;
  cursor: pointer;
  text-transform: uppercase;
}

.usersTitle{
  font-size: 18px;
  text-transform: uppercase;
}

.userModalContainer{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.userModal{
  max-width: 600px;
  width: 100%;
  padding: 40px;
  background: #fff;
  margin: 0 auto;
}

.userModalTitle{
  margin: 0 0 15px;
}

.modalInput{
  padding: 10px 14px;

}

.modalBtnsContainer{
  max-width: 300px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 20px auto 0;
}

.modalBtn{
  border: 0;
  max-width: 100px;
  width: 100%;
  padding: 10px 15px;
  color: #ffffff;
  background: #82B541;
  cursor: pointer;
}

.redBtn{
  background: #ff3019;;
}

.usersWrapper{
  display: flex;
  flex-wrap: wrap;
}

.userContainer{
  border: 1px solid #000;
}

.nameContainer{
  position: relative;
  padding: 5px 80px;
  border-bottom: 1px solid #000;
}

.delUserBtn, .editUserBtn{
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  top: 0;
  background: transparent;
  border: 0;
  cursor: pointer;
  height: 20px;
  font-size: 18px;

}

.editUserBtn{
  font-size: 22px;
  right: 30px;
  color: green;
  transform: scaleX(-1);
  top: -4px;
}
